.get-color {
  display: inline-block;
  border-radius: 100%;
  height: 20px;
  width: 20px;

  @each $name, $color in $colors {
    &--#{$name} {
      background: $color;
    }
  }
}

.text-color {
  @each $name, $color in $colors {
    &--#{$name} {
      color: $color;
    }
  }
}


