.ui {
  &-accordion {
    &__item {

      &--active > &__content {
        display: block;
      }

      &__head {
        display: flex;
        align-items: center;
        cursor: pointer;

        &:after {
          display: block;
          width: 100%;
          height: 1px;
          content: '';
          background: #dedede;
          flex: 1 0px;
          order: 1;
          margin: 0 10px;
        }

        & > .down {
          margin-top: -3px;
          font-size: 12px;
          color: #2d9e6e;
          display: flex;
          order: 2;
          transition: transform .2s ease 0s;
        }

        & > .up {
          transform: rotate(180deg);
        }
      }

      &__content {
        display: none;
      }
    }
  }
}


