.icons {
  display: inline-table;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;

  &--reate-up {
    background-position: -88px 0px;
    width: 14px;
    height: 14px;
  }
  &--reate-down {
    background-position: -102px 0px;
    width: 14px;
    height: 14px;
  }
  &--search {
    background-position: -75px 0px;
    width: 14px;
    height: 14px;
  }
  &--calendar {
    width: 18px;
    height: 14px;
    background-position: -75px -14px;
  }
  &--edit {
    width: 10px;
    height: 13px;
    background-position: -23px -75px;

    &--green {
      background-position: -34px -75px;
    }
  }

  &--down {
    width: 10px;
    height: 6px;
    background-position: -78px -30px;
  }

  &--delete {
    width: 8px;
    height: 13px;
    background-position: 0px -75px;

    &--green {
      background-position: -13px -75px;
    }
  }

  &--photo {
    width: 19px;
    height: 13px;
    background-position: -76px -55px;

    &--green {
      background-position: -95px -75px;
    }
  }

  &--barcode {
    width: 19px;
    height: 13px;
    background-position: -76px -70px;

    &--green {
      background-position: -95px -70px;
    }
  }

  &--description {
    width: 19px;
    height: 13px;
    background-position: -76px -84px;

    &--green {
      background-position: -95px -84px;
    }
  }

  &--info {
    width: 4px;
    height: 11px;
    background-position: -76px -38px;
  }

  &--tag {
    width: 12px;
    height: 11px;
    background-position: -93px -38px;
  }

  &--social {
    &--instagram {
      width: 17px;
      height: 17px;
      background-position: 0 0;

      &--color {
        background: #dc3c78;
        border: 1px solid #dc3c78;

        &:hover {
          border: 1px solid #dc3c78 !important;
        }
      }
    }
    &--vk {
      width: 22px;
      height: 14px;
      background-position: -17px 0;

      &--color {
        background: #4d76a4;
        border: 1px solid #4d76a4;

        &:hover {
          border: 1px solid #4d76a4 !important;
        }
      }
    }
    &--twitter {
      width: 17px;
      height: 17px;
      background-position: -40px 0;

      &--color {
        background: #55acef;
        border: 1px solid #55acef;

        &:hover {
          border: 1px solid #55acef !important;
        }
      }
    }
    &--fb {
      width: 10px;
      height: 17px;
      background-position: -57px 0;

      &--color {
        background: #3b5999;
        border: 1px solid #3b5999;

        &:hover {
          border: 1px solid #3b5999 !important;
        }
      }
    }
    &--ok {
      width: 12px;
      height: 20px;
      background-position: 0 -17px;

      &--color {
        background: #f38331;
        border: 1px solid #f38331;

        &:hover {
          border: 1px solid #f38331 !important;
        }
      }
    }
    &--mailru {
      width: 19px;
      height: 19px;
      background-position: -12px -17px;

      &--color {
        background: #198de4;
        border: 1px solid #198de4;

        &:hover {
          border: 1px solid #198de4 !important;
        }
      }
    }
    &--gplus {
      width: 22px;
      height: 15px;
      background-position: -31px -17px;

      &--color {
        background: #d94f44;
        border: 1px solid #d94f44;

        &:hover {
          border: 1px solid #d94f44 !important;
        }
      }
    }
    &--youtube {
      width: 22px;
      height: 16px;
      background-position: -53px -17px;

      &--color {
        background: #d82624;
        border: 1px solid #d82624;

        &:hover {
          border: 1px solid #d82624 !important;
        }
      }
    }

    &--white {
      &.icon--social--instagram {
        @extend .icon--social--instagram !optional;
        background-position: 0 -38px;
      }
      &.icon--social--vk {
        @extend .icon--social--vk !optional;
        background-position: -17px -38px;
      }
      &.icon--social--twitter {
        @extend .icon--social--twitter !optional;
        background-position: -40px -38px;
      }
      &.icon--social--fb {
        @extend .icon--social--fb !optional;
        background-position: -57px -38px;
      }
      &.icon--social--ok {
        @extend .icon--social--ok !optional;
        background-position: 0 -55px;
      }
      &.icon--social--mailru {
        @extend .icon--social--mailru !optional;
        background-position: -12px -55px;
      }
      &.icon--social--youtube {
        @extend .icon--social--youtube !optional;
        background-position: -53px -55px;
      }
      &.icon--social--gplus {
        @extend .icon--social--gplus !optional;
        background-position: -31px -55px;
      }
    }
  }
}

.social {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  @extend .ui-btn !optional !optional;
  @extend .ui-btn--circle !optional !optional;

  height: 45px;
  width: 45px;

  &--default {
    background: #f3f3f3;
    border: 1px solid #dbdbdb;
  }
}

.ico {
  color: #36a374;
}


